import { test, expect, Page } from '@playwright/test';
// 인라인 헬퍼 함수에 타입 추가
async function login(page: Page) {
  await page.goto('/login');
  await page.fill('[data-testid="email-input"]', 'test@example.com');
  await page.fill('[data-testid="password-input"]', 'Test123!');
  await page.click('[data-testid="login-button"]');
  await page.waitForSelector('[data-testid="welcome-message"]');
}

test.describe('감정 공유 기능', () => {
  test.beforeEach(async ({ page }) => {
    // 테스트 사용자로 로그인
    await login(page);
  });

  test('감정 선택 및 게시글 작성', async ({ page }) => {
    // 홈 화면으로 이동
    await page.goto('/home');
    
    // 감정 선택
    await page.click('[data-testid="emotion-happy"]');
    
    // 게시글 작성
    await page.fill('[data-testid="post-content"]', '오늘은 정말 행복한 하루입니다.');
    await page.click('[data-testid="post-submit"]');
    
    // 게시글 확인
    await expect(page.locator('[data-testid="post-list"]')).toContainText('오늘은 정말 행복한 하루입니다.');
    
    // 감정 태그 확인
    await expect(page.locator('[data-testid="emotion-tag"]')).toContainText('행복');
  });
});